
.timeline{list-style-type:none;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;padding-left:0px
}
.li{transition:all 200ms ease-in;width:18%
}
.timestamp{margin-bottom:20px;padding:0px 40px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center;font-weight:100
}
.status{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;border-top:2px solid #D6DCE0;position:relative;transition:all 200ms ease-in
}
.status h4{font-weight:600
}
.status:before{content:'';width:25px;height:25px;background-color:white;border-radius:25px;border:1px solid #ddd;position:absolute;top:-15px;left:42%;transition:all 200ms ease-in
}
.li.complete .status{border-top:2px solid #66DC71
}
.li.complete .status:before{background-color:#66DC71;border:none;transition:all 200ms ease-in
}
.li.complete .status h4{color:#66DC71
}
@media (min-device-width: 320px) and (max-device-width: 700px){
.timeline{list-style-type:none;display:block
}
.li{transition:all 200ms ease-in;display:-ms-flexbox;display:flex;width:inherit
}
.timestamp{width:100px
}
.status:before{left:-8%;top:30%;transition:all 200ms ease-in
}
}



